.header {
    border-bottom: 1px solid #d2d6df;
}
.header__container-logo{
    width: 60px;
    height: 50px;
    border-right: 1px solid #d2d6df;
    margin: 8px;
    padding-right: 4px; 
}
.logo {
    width: 50px;
    height: 50px;
}

@media all and (min-width:1024px) {
    .header__container-logo{
        width: 110px;
        height: 100px;
    }
    .logo {
        width: 100px;
        height: 100px;
    }    
}